.visually-hidden {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px !important;
  width: 1px !important;
  border: 0 !important;
  padding: 0 !important;
  position: absolute !important;
}

body {
  color: #2d2a26;
  letter-spacing: .03em;
  font-family: Avenir Next, sans-serif;
  font-size: 14px;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

h3 {
  color: #2d2a26;
  font-size: 18px;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  margin: 0;
  padding: 0;
  display: block;
}

address {
  letter-spacing: .03em;
  color: #2d2a26;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.71;
}

.container {
  max-width: 375px;
}

.header {
  background-color: #fff;
  border-bottom: 1px solid #2d2a26;
}

.line-move {
  background: linear-gradient(90deg, #274cd6 0%, #cf63c6 49.91%, #80d7c4 100%);
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.line-move__text {
  align-items: center;
  font-size: 12px;
  display: flex;
}

.line-move__images {
  width: 15px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 5px;
  margin-right: 5px;
}

@keyframes scroll {
  0% {
    transform: translate(-5%);
  }

  100% {
    transform: translate(-200%);
  }
}

.marquee {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
}

.marquee span {
  -webkit-animation: scroll 6s linear infinite;
  -moz-animation: scroll 6s linear infinite;
  padding-left: 100%;
  animation: scroll 8s linear infinite;
}

.btn-nav {
  align-items: center;
  padding: 14px 13px 9px 18px;
  display: flex;
}

.btn-nav__get-kit {
  width: 140px;
  height: 53px;
  border: 1px solid #2d2a26;
  border-radius: 10px;
  margin-left: auto;
  padding: 16px 27px;
  display: flex;
  box-shadow: 4px 4px #2d2a26;
}

.get-kit__images {
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 9px;
}

.btn-nav__burger-menu {
  width: 54px;
  height: 54px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  display: block;
}

.burger-menu__icon {
  width: 54px;
  height: 54px;
  fill: #2d2a26;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

